<template>
  <div>
    <div class="container">
      <h1>Ubicación</h1>
    </div>
    <location-table
        :data="locationListState"
        @DeleteLocation="DeleteLocation"
        @addLocation="addLocation"
        @updateLocation="updateLocation"
    />
  </div>
</template>

<script>

import LocationTable from "@/components/location/LocationTable";
import {mapActions, mapGetters} from "vuex";
export default {
  name: "Location",
  components: {
    LocationTable
  },
  data () {
    return {
      jsonDataUpdate: {
        code: "",
        id: 0,
        location: "",
        timesTamp: ""
      }
    }
  },
  created() {
    this.callGetLocationState()
  },
  computed: {
    ...mapGetters(['locationListState'])
  },
  methods: {
    ...mapActions(['getLocationState', 'setLocationnew', 'apiUpdateLocation', 'apiDeleteLocation']),
    async updateLocation( item) {
      this.jsonDataUpdate.code = item.code;
      this.jsonDataUpdate.id = item.id;
      this.jsonDataUpdate.location = item.location;
      this.jsonDataUpdate.timesTamp = item.timesTamp;
      await this.apiUpdateLocation(this.jsonDataUpdate)
      await this.callGetLocationState()
    },

  async  addLocation( item) {
    await this.setLocationnew(item)
     await this.callGetLocationState()
    },
    async  DeleteLocation(id) {
     await this.apiDeleteLocation(id)
      await this.callGetLocationState()
    },
   async callGetLocationState() {
      await  this.getLocationState()
    }
  }
}
</script>

